.login {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-image: url(/sino-web-js/static/media/backimg.8cf88c99.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: 100vh;
}
.login .login-header {
  box-sizing: border-box;
  padding: 8px;
  display: flex;
  justify-content: space-between;
  line-height: 68px;
}
.login .login-header .sino-ul {
  margin-right: 36px;
}
.login .login-header .sino-ul a {
  font-size: 1.3em;
  color: #fff;
  margin: 0 24px;
}
.login .login-header .log-style {
  max-height: 36px;
  margin: 16px 60px;
}
.login .sino-icon-login img {
  width: 100px;
  margin-left: 8em;
}
.login div.login-content {
  box-sizing: border-box;
  margin-left: 10em;
  display: flex;
  margin-top: 8px;
}
.login div.login-content img.img-content {
  width: 800px;
}
.login div.login-content .login-card {
  border: 0;
  margin-left: 14em;
  width: 400px;
  min-width: 400px;
  box-shadow: 2px 2px 10px 0px #3c3b3b;
}
.login div.login-content .login-card div.ant-card-body {
  padding: 0;
}
.login div.login-content .login-center .login-title {
  padding: 16px 0;
  text-align: center;
  background-color: #1d78da;
  color: #fff;
}
.login div.login-content .login-center .login-title .login-text {
  color: #fff;
  font-weight: 700;
  margin-bottom: 0;
}
.login div.login-content .login-center #normal_login {
  padding: 32px 24px 0;
}
.login div.login-content .login-center div.login-button {
  margin-bottom: 8px;
}
.login div.login-content .login-center .login-form-button {
  margin-top: 8px;
  border-radius: 5px;
  font-size: 18px;
}
.login div.login-content .login-center .text-right-password {
  color: #989898;
  margin-bottom: 8px;
  text-align: right;
}
.login div.login-content .login-center .text-right-password span {
  cursor: pointer;
}
.login div.login-content .login-footer-text {
  border-top: solid #ccc 0.8px;
  padding: 8px 24px;
  color: #bbbbbb;
}
.login .footer {
  text-align: right;
  margin: 32px 132px;
}
.login .footer span.anticon-comment {
  padding-right: 8px;
  font-size: 24px;
  vertical-align: text-top;
}
.login .footer div.footer-button {
  display: inline-block;
  width: 156px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 18px;
  background: #1d78da;
  border-radius: 32px;
  color: #fff;
  cursor: pointer;
}
.footer-menu {
  padding: 16px 8px;
  width: 400px;
}
.footer-menu li > div {
  display: flex;
}
.footer-menu li > div span.anticon {
  font-size: 32px;
  line-height: 46px;
}
.footer-menu li > div div {
  margin-left: 8px;
  text-align: left;
}
.footer-menu li > div div span:first-child {
  font-size: 18px;
}
.footer-menu li > div div span:last-child {
  font-size: 14px;
}
.footer-menu li > div div span {
  display: block;
}
.login-modal {
  width: 456px !important;
}
.login-modal div.ant-modal-confirm-body span.ant-modal-confirm-title {
  text-align: center;
  font-size: 30px;
  font-weight: 600;
}
.login-modal div.ant-modal-confirm-body .ant-modal-confirm-content {
  font-size: 18px;
}
.login-modal div.ant-modal-confirm-btns {
  float: none;
  text-align: center;
}
.login-modal div.ant-modal-confirm-btns button {
  width: 80px;
  height: 40px;
  font-size: 1.4em;
  border-radius: 5px;
}
.login-modal div.ant-modal-confirm-btns button:first-child {
  display: none;
}
