.ant-layout-header {
  transition: width 0.2s;
  z-index: 9;
  padding: 0 24px;
  position: relative;
  height: 64px;
  background: #fff !important;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
}
.ant-layout-header .right-menu {
  float: right;
}
.ant-layout-header .right-menu .dropdown-wrap {
  cursor: pointer;
  display: inline-block;
}
.ant-dropdown-menu-item .anticon:first-child {
  font-size: 14px;
}
.user-info {
  display: flex;
  padding: 4px 12px;
}
.user-info .user-image {
  margin-right: 8px;
}
.user-info .user-text span {
  display: block;
}
.user-info .user-text span:first-child {
  font-size: 16px;
  color: #455a64;
}
.user-info .user-text span:last-child {
  font-size: 12px;
  color: #99abb4;
}
